.calculator {
  width: 300px;
  margin: 50px auto;
  padding: 20px;
  border-radius: 8px;
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
  background-color: #f0f0f0;
}

.display {
  width: calc(100% - 20px);
  height: 50px;
  margin-bottom: 20px;
  padding: 10px;
  text-align: right;
  font-size: 24px;
  background-color: #fff;
  border: 1px solid #ddd;
  border-radius: 4px;
}

.buttons {
  display: grid;
  grid-template-columns: repeat(4, 1fr);
  gap: 10px;
}

.button {
  padding: 15px;
  font-size: 18px;
  border: none;
  border-radius: 4px;
  cursor: pointer;
  background-color: #e0e0e0;
  transition: background-color 0.3s;
}

.button:hover {
  background-color: #d0d0d0;
}

.wide {
  grid-column: span 2;
}